@import '~node_modules/modern-normalize/modern-normalize.css';
@import '~node_modules/swiper/swiper.min.css';

body {
  font-family: 'Lexend', sans-serif;
  font-weight: 600;
  font-size: 16px;
  letter-spacing: 0.02em;

  background-color: $color-white;
}

.wraper {
  position: relative;
  max-width: 1440px;
  margin: 0 auto;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}
