.mobile-nav {
  flex-direction: column;
  row-gap: 15px;
  display: flex;
}

.mobile-nav__link {
  letter-spacing: .02em;
  color: #1b1c57;
  font-size: 18px;
  line-height: 1.17;
  position: relative;
}

.mobile-nav__link:after {
  content: "";
  width: 0;
  height: 1px;
  background-color: #1b1c57;
  transition: width .6s, left .6s;
  position: absolute;
  bottom: -1px;
  right: 0;
}

.mobile-nav__link:hover:after, .mobile-nav__link:focus:after {
  width: 100%;
  left: 0;
}

.mobile-nav__list-prop {
  flex-direction: column;
  row-gap: 10px;
  padding: 10px 15px;
  display: flex;
}

